@use 'base/variables' as v;
@use 'base/mixins' as m;

.dark-mode {
    background-color: darken(v.$grisOscuro, 2);
    a {
        color: v.$blanco;
    }

    p, h1, h2, h3, h4 {
        color: v.$gris;
    }

    .anuncio,
    .resumen-propiedad {
        background-color: v.$grisOscuro;
        border: 1px solid v.$grisOscuro;
    }

    .iconos-caracteristicas {
        img {
            filter: invert(75%);
        }
    }

    .precio{
        color: v.$verde;
    }

    .texto-nosotros blockquote {
        color: v.$blanco;
    }

    .formulario {
        label,
        legend,
        p {
            color: v.$gris;
        }

        input:not([type="submit"]),
        textarea,
        select {
            background-color: v.$grisOscuro;
            color: v.$blanco;
            border: 1px solid v.$grisOscuro;
        }

        fieldset {
            border: 1px solid darken(v.$gris, 50);
        }

        input[type="date"]::-webkit-calendar-picker-indicator,
        input[type="time"]::-webkit-calendar-picker-indicator {
            filter: invert(1);
        }
    }

    .alerta {
        &.error{
            background-color: v.$rojoOscuro;
        }
    }

    table.propiedades {
    
        thead {
         
            th {
                color: v.$blanco;
                border: 0.5px solid darken(v.$gris,50);
            }
        }
    
        tbody {
            td {
                color: v.$blanco;
                border: 0.5px solid darken(v.$gris,50);
            }
        }
    }
}