@use 'base/variables' as v;
@use 'base/mixins' as m;

@include m.tablet {
    .contenedor-anuncios {
        @include m.grid(3, 2rem);
    }
}

.anuncio {
    background-color: lighten(v.$gris,5);
    border: 1px solid lighten(v.$grisOscuro,65);

    .contenido-anuncio {
        padding: 2rem;
    }

    h3, p{
        margin: 0;
    }
}

.descripcion {
    text-align: justify;
}

.precio {
    color: v.$verde;
    font-weight: bold;
    font-size: 3rem;
    text-align: center;
}

.iconos-caracteristicas {
    display: flex;
    list-style: none;
    padding: 0;
    max-width: 40rem;

    li {
        flex: 1;
        display: flex;
        justify-content: center;

        img {
            flex: 0 0 1rem;
            margin-right: 1rem;
            width: auto;
        }

        p {
            font-weight: v.$black;
        }
    }
}

.contenido-anuncio {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include m.tablet {
        min-height: 65rem;
    }

    @include m.desktop {
        min-height: 50rem;
    }
}

.resumen-propiedad {
    padding: 0 2rem;
}

.contenido-pagina{
    margin-bottom: 5rem;
}