@use "base/variables" as v;
@use "base/mixins" as m;

.contenido-nosotros {
  @include m.tablet {
    @include m.grid(2, 3rem);
  }
}

.texto-nosotros blockquote {
    font-weight: v.$black;
    font-size: 2.2rem;
    margin: 0;
    padding-top: 2rem;

    @include m.tablet {
        padding-top: 0;
    }
}