@use 'base/mixins' as m;
@use 'base/variables' as v;

.derecha {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include m.tablet {
        flex-direction: row;
        justify-content: flex-end;
    }
}

.dark-mode-boton {
    width: 3rem;
    margin-top: 1rem;
    margin-bottom: 3rem;
    filter: invert(80%);

    &:hover {
        cursor: pointer;
    }

    @include m.tablet {
        margin: 0;
        margin-left: 2rem;
    }
}

.barra {
    display: flex;
    flex-direction: column;
    padding-top: 3rem;
    align-items: center;
    @include m.tablet {
        flex-direction: row;
        justify-content: space-between;
    }
}

.navegacion {

    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility 0.7s ease-in-out, opacity .4s linear, height 0.4s linear 0.2s;
    // transition: all 2s;

    @include m.tablet {
        opacity: 1;
        visibility: visible;
        display: flex;      
        align-items: center;
    }
    a {
        color: v.$blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;

        &:hover {
            color: v.$verde;
        }

        @include m.tablet {
            margin-right: 2rem;
            font-size: 1.8rem;

            &:last-of-type {
                margin-right: 0;
            }
        }

    }
}


/* Men√∫ de hamburguesa */
.mobile-menu {

    margin: 4rem 0;
    @include m.tablet {
        display: none;
    }

    img {
        width: 5rem;
    }
}

.mostrar {
    visibility: visible;
    opacity: 1;
    height: 20rem;
    @include m.tablet {
        height: auto;
    }
    transition: visibility 0.7s ease-in-out, opacity 0.2s linear 0.2s, height 0.2s linear;
    // &+.dark-mode-boton {
    //     margin-top: 4rem;
    // }
}