@use 'base/variables' as v;
@use 'base/mixins' as m;

.alerta {
    padding: .5rem;
    text-align: center;
    color: v.$blanco;
    font-weight: v.$black;
    text-transform: uppercase;
    margin: 1rem 0;
    &.error {
        background-color: v.$rojo;
    }
    &.exito {
        background-color: v.$verde;
    }
}

table.propiedades {
    width: 100%;
    border-spacing: 0;

    thead {
        background-color: v.$verde;

        th {
            color: v.$blanco;
            padding: 2rem;
            border: 0.5px solid v.$gris;
        }
    }

    .imagen-tabla {
        width: 12rem;
        margin-left: auto;
        margin-right: auto;
    }

    tbody {
        td {
            text-align: center;
            border: 0.5px solid v.$gris;
            a,
            input{
                height: 5rem;
                padding: 1rem;
                width: 80%;
                margin-left: auto;
                margin-right: auto;
                margin-bottom: calc(v.$separacion/2);
            }
        }
    }
}

.imagen-small {
    width: 20rem;
}

.tabla-opciones{
    display: flex;
    justify-content: space-between;
}